export const getTitleDropdownByCount = (values, type) => {
  let arr = values.map((item) => item.count)
  let handleValue = values.map((val) => val.count).every((e) => e === 0)
  let g = arr[1] + arr[0] > 4 ? '' : arr[1] + arr[0] === 1 ? '' : ''
  let m = arr[2] > 4 ? '' : arr[2] === 1 ? '' : ''
  let z = arr[0] + arr[1] !== 0 ? ', ' : ''
  let r = arr[0] > 4 ? '' : arr[0] === 1 ? '' : ''
  let b = arr[1] > 4 ? '' : arr[1] === 1 ? '' : ''
  let v = arr[2] > 4 ? '' : arr[2] === 1 ? '' : ''
  let z1 = arr[0] !== 0 ? ', ' : ''
  let z2 = arr[1] !== 0 ? ', ' : z1
  let result = ''
  type === 'guests'
    ? handleValue
      ? (result = ' ')
      : (result = `
      ${arr[0] + arr[1] !== 0 ? arr[0] + arr[1] + ' ' + g : ''}${arr[2] !== 0 ? z + arr[2] + ' ' + m : ''}
    `)
    : handleValue
    ? (result = ' ')
    : (result = `
      ${arr[0] !== 0 ? arr[0] + ' ' + r : ''}${arr[1] !== 0 ? z1 + arr[1] + ' ' + b : ''}${
        arr[2] !== 0 ? z2 + arr[2] + ' ' + v : ''
      }
    `)
  return result
}
