import React from 'react'
import Comment from '../Comment'
import Datails from '../UI/Datails'
import Diagram from '../UI/Diagram'
import BulletList from '../UI/BulletList'
import * as S from './style'
import { ReactComponent as Comfort } from '../../assets/icons/comfort.svg'
import { ReactComponent as Convenience } from '../../assets/icons/convenience.svg'
import { ReactComponent as Cosiness } from '../../assets/icons/cosiness.svg'

const variables = [
  {
    title: '',
    subtitle: ' ',
    icon: <Comfort />,
  },
  {
    title: '',
    subtitle: '    ',
    icon: <Convenience />,
  },
  {
    title: '',
    subtitle: '  ',
    icon: <Cosiness />,
  },
]

const bulletListValues = [
  '  ',
  '   ',
  '    13:00,    12:00',
]

const comments = [0, 1]

const PageSection = ({ reviews }) => {
  return (
    <S.PageSection>
      <S.UpperBlock>
        <S.Info>
          <h5>  </h5>
          <S.DetailsBox>
            {variables.map((item) => (
              <Datails title={item.title} subtitle={item.subtitle} icon={item.icon} key={item.title} />
            ))}
          </S.DetailsBox>
        </S.Info>
        <S.RateDiagram>
          <h5>  </h5>
          <Diagram reviews={reviews} />
        </S.RateDiagram>
      </S.UpperBlock>

      <S.CommentBlock>
        <S.BlockTitle>
          <h5>  </h5>
          <span>{comments.length} </span>
        </S.BlockTitle>
        {comments.map((item) => (
          <Comment index={item} key={item} />
        ))}
      </S.CommentBlock>
      <S.NoteBlock>
        <BulletList title='' values={bulletListValues} />
        <S.Cancel>
          <h5></h5>
          <p>
                48 .         5 .    
                  .
          </p>
        </S.Cancel>
      </S.NoteBlock>
    </S.PageSection>
  )
}

export default PageSection
