import React from 'react'
import * as S from './style'
import Calendar from '../UI/Calendar'
import Dropdown from '../UI/Dropdown'
import Slider from '../UI/Slider'
import DefaultCheckbox from '../UI/Checkbox/Default'
import RichCheckbox from '../UI/Checkbox/Rich'
import { initialValues } from './data'
import { useSelector } from 'react-redux'
const Sidebar = () => {
  const updatingInitialValues = useSelector((state) => state.filter)
  return (
    <S.Sidebar>
      <Calendar
        date='filter'
        start={'   '}
        initialValueEntryDate={updatingInitialValues.entryDate}
        initialValueEndDate={updatingInitialValues.endDate}
      />
      <Dropdown type='guests' title='' margin='20px 0 30px' initialValues={updatingInitialValues.guests} />
      <Slider title=' ' initialMin={initialValues.slider[0]} initialMax={initialValues.slider[1]} />
      <p>     </p>
      <DefaultCheckbox title=' ' margin='30px 0' initialValues={initialValues.default} />
      <RichCheckbox title='' initialValues={initialValues.rich} />
      <Dropdown type='rooms' title=' ' margin='30px 0' initialValues={updatingInitialValues.rooms} />
      <DefaultCheckbox title=' ' type='list' initialValues={initialValues.list} />
    </S.Sidebar>
  )
}

export default Sidebar
